body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h2 {
    margin: 0;
}

/* Full panel */
.main-panel {
    margin: 0px auto;
    padding: 0;
    width: 1000px;
}

/* Graph panel */
canvas {
    border: 0;
    margin: 0;
    padding: 0;
}

#graph-space {
    background-color: #d3d3d3;
    border: 0;
    height: 100%;
    margin: 0;
    padding: 0;
    width: 100%;
}

/* Instruction panel */
.instruction-panel {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    height: 100%;
}

.instruction-file-section {
    width: 100%;
}

.instruction-nav-button {
    /* height: 5%; */
    width: 100%;
}

.instruction-section {
    height: 450px;

    overflow-x: hidden;
    overflow-y: scroll;
    width: 100%;
}

/* Resource Table */
.resource-acquire-button {
    height: 100%;
    width: 100%;
}

